import { AuthorisationService } from '../../Services/authorisation.service';
import { Component, OnInit } from '@angular/core';
import { ErrorHandling } from '../../Classes/ErrorHandling';
import { FormControl } from '@angular/forms';
import { Inject } from '@angular/core';
import { InputValidation } from '../../Classes/InputValidation';
import { LoginService } from '../../Services/login.service';
import { MAT_DIALOG_DATA, MatDialog} from '@angular/material';
import { MatDialogRef } from '@angular/material';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { Validators } from '@angular/forms';
import { Observable } from 'rxjs/internal/Observable';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [LoginService]
})
export class LoginComponent implements OnInit{
  public hide = true;
  public code = '';
  public type  = 0;
  public loading = false;
  public tokenValue = 'UNKNOWN';

  constructor(
    private connexion: LoginService,
    private authorisation: AuthorisationService,
    private route: Router,
    public errorMsg: MatSnackBar,
    public dialog: MatDialog,
  ) {}

  public ngOnInit(): void{
  }

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(
      /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
  ]);

  passwordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);

  matcher = new InputValidation();
  matcherPassword = new InputValidation();

  /** If the user has already a valid code**/
  public hasCode() {
    const message = 'If your code lived more than an hour, pleaze ask for another code ';
    // show the user a dialog to enter the code
    this.openCodeDialog(message);
  }

  /* If the user click on login button*/
  public onSubmit() {
    /** if the user submit show him a dialog to choose how to get code : sms or email*/
    this.openChooseCodeDialog();
  }

  /* If the user click on the eye icon to show/hide password */
  public showPassword() {
    this.hide = !this.hide;
  }

  public openChooseCodeDialog() {
    /** open the dialog **/
    const dialogRef = this.dialog.open(ChooseCodeDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    /** when the dialog is closed show the user a dialog where he can enter the verification code**/
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1 || result == 0 ) {
        this.type = result;
        this.loading = true;
        this.chooseManner();
      } else {
        let errMsg;
        errMsg = new ErrorHandling(this.errorMsg);
        errMsg.handleError('unkown error');
      }
    });
  }

  public chooseManner(): void{
    const msg = new ErrorHandling(this.errorMsg);

    this.connexion.login(this.emailFormControl.value, this.passwordFormControl.value, this.type).subscribe(
      data => {
        if (this.type == 0) {
          msg.showErrorMsg('Check your email', this.emailFormControl.value);
        } else {
          msg.showErrorMsg('Check your phone', '');
        }
        this.loading = false;
        const message = 'Still waiting for the code ?';
        this.openCodeDialog(message);
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public openCodeDialog(message) {
    /** open the dialog and transmit some data into : type : mail or sms, email and the password**/
    const dialogRef = this.dialog.open(CodeDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : {
        type : this.type ,
        email : this.emailFormControl.value,
        password : this.passwordFormControl.value,
        message:  message
      }
    });

    /** when closing the dialog send call the sendCodefunction to send the code to the backend**/
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.code = result;
        this.sendCode();
      } else {
        let errMsg;
        errMsg = new ErrorHandling(this.errorMsg);
        errMsg.handleError('unkown error');
      }
    });
  }

  public sendCode(): boolean {
    this.loading = true;
    let success = false;
    /** send the code by calling the authorisation function of the authorisation service **/
    this.authorisation.authorisation(this.code, this.emailFormControl.value, this.passwordFormControl.value).subscribe(
      data => {
        /** if the request succeeded, store the token, and redirect the user to the appropriate route**/
        this.authorisation.setToken(data['access_token'],data['refresh_token'], data['user_type'],data['user_id']);
        if (data['user_type'] == 2) {
          this.route.navigate(['/manager/' + data['user_id'] + '/dashboard']);
        } else if (data['user_type'] == 1) {
          this.route.navigate(['/banker/' + data['user_id'] + '/home']);
        } else {
          let errMsg;
          errMsg = new ErrorHandling(this.errorMsg);
          errMsg.showErrorMsg('unkown user type', 'resend code');
        }
        this.loading = false;
        success = true;
      },
      error => {
        const message = 'Invalid code, reinter it, or';
        this.openCodeDialog(message);
        let errMsg;
        errMsg = new ErrorHandling(this.errorMsg);
        errMsg.handleError(error);
        this.loading = false;
      }
    );
    return success;
  }
}

/** dynamic component : chooseCodeDialog **/
@Component({
  selector: 'app-choose-code-dialog',
  templateUrl: 'chooseCodeDialog.html',
  styleUrls: ['chooseCodeDialog.css'],
})
export class ChooseCodeDialogComponent {
  constructor(
    public dialogRef: MatDialogRef<ChooseCodeDialogComponent>) {
    }

  type = new FormControl('', [
    Validators.required
  ]);

  onSubmit(choix) {
    this.dialogRef.close(choix);
  }
  onExit() {
    this.dialogRef.close(-1);
  }
}

/** dynamic component : codeDialog **/
@Component({
  selector: 'app-code-dialog',
  templateUrl: 'codeDialog.html',
  styleUrls: ['codeDialog.css'],
  providers: [LoginService]
})

export class CodeDialogComponent {

  public code = '';
  public message = '';

  /** form for the code : length= 4, number*/
  public codeFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(4),
    Validators.maxLength(4),
    Validators.pattern(/[0-9]+/)
  ]);

  public matcher = new InputValidation();
  constructor(
    public errorMsg: MatSnackBar,
    private connexion: LoginService,
    public dialogRef: MatDialogRef<CodeDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      this.message = this.data.message;
  }
  
  public onSubmit(code): void{
      this.dialogRef.close(code);
  }

  public getCode(): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.connexion.login(this.data.email, this.data.password, this.data.type).subscribe(
      data => {
        if (this.data.type == 0) {
          msg.showErrorMsg('Check your email', this.data.email);
        } else if (this.data.type == 1) {
          msg.showErrorMsg('Check your phone', '');
        }
      },
      error => {
        msg.handleError(error);
      }
    );
  }
}
