import { NgModule} from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './components/home/login.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { sectionRoutesBanker } from './components/banker/bankersRoutes';
import { sectionRoutesManager } from './components/manager/ManagerRoutes';
import { AuthGuardService } from './Services/auth-guard.service';
import { BankerComponent } from './components/banker/banker.component';
import { ManagerComponent } from './components/manager/manager.component';


const thrRoutes: Routes = [
  {
    path: '',   redirectTo: 'home', pathMatch: 'full'
  },
  {
    path: 'home', component: LoginComponent
  },
  {
    path: 'banker/:id',
    component: BankerComponent,
    children: sectionRoutesBanker,
    // canActivate: [AuthGuardService],
  },
  {
    path: 'manager/:id',
    component: ManagerComponent,
    children : sectionRoutesManager,
    // canActivate: [AuthGuardService],
  },
  {
    path: '**', component: PageNotFoundComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      thrRoutes
    )
  ],
  exports: [
    RouterModule
  ]
})
export class TharwaRoutingModule {}
