import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
import { Banker } from '../Classes/Banker';

@Injectable()
export class AddBankerService {

  private tharwaURL = 'bankers'; /** DNS/bankers to add a banker**/
  private upLoadURL = 'user/photo';

  constructor(private http: RequestHandlerService) { }

  public addBanker(banker) {
   /** return the http post request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPostRequest(this.tharwaURL, banker);
  }

  public postImage(fileToUpload: File, id) {

    const formData: FormData = new FormData();
    formData.append('photo', fileToUpload);
    formData.append('id_user', id);
    return this.http.putImage(this.upLoadURL, formData);
}
}
