import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class EditProfileService {

  private tharwaURLManager = 'managers'; /** DNS/manager/{id} to update the manager with the id : id**/
  private tharwaURLBanker = 'bankers'; /** DNS/banker/{id} to update the banker with the id : id**/
  private upLoadURL = 'user/photo';
  constructor(private http: RequestHandlerService) { }

  public editManger(manager, id) {
    return this.http.httpPutRequest(this.tharwaURLManager , manager);
  }

  public editBanker(banker, id) {
    return this.http.httpPutRequest(this.tharwaURLBanker, banker);
  }

  public postImage(fileToUpload: File, id) {

    const formData: FormData = new FormData();
    formData.append('photo', fileToUpload);
    return this.http.putImage(this.upLoadURL, formData);
  }
}
