import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { CookieService } from 'ngx-cookie-service';
import { Router } from '@angular/router';

@Injectable()
export class RequestHandlerService {
  readonly tharwaURL = 'https://serene-retreat-29274.herokuapp.com/';
  //readonly tharwaURL = 'http://192.168.43.5/';

  readonly imgUrl = 'images/customer_min/';
  readonly justifUrl = 'images/justificatif_vrm_min/';
  public token = '';
  public timeTolive = '';

  constructor(
    private http: HttpClient,
    private cookieService: CookieService,
    private router: Router
  ) { }

  public httpGetRequest(url: string): Observable<any> {
    /**
     * add the httpheader  :
     * every get request must have a token
     * **/
    const headers =  new HttpHeaders({
      'Accept' : 'application/json'
    });
    /**
     * send the get token
     * **/
    return this.http.get(this.tharwaURL + url,/* {headers}*/);
  }

 public  httpPostRequest(url: string, params): Observable<any> {
    /**
     * add the httpheader  :
     * the first two requests  /login and /login/code the token is empty
     * after the authentification, the token is set and should be send with every request
     * **/
    
    const headers =  new HttpHeaders({
      'Accept' : 'application/json'
    });
    /** send the post request **/
    return this.http.post(this.tharwaURL + url, JSON.stringify(params),/*{headers}*/);
  }

  public httpPutRequest (url: string, params): Observable<any> {
    /**
     * add the httpheader  :
     * every put request must have a token
     * **/
    
    const headers =  new HttpHeaders({
      'Accept' : 'application/json'
    });

    return this.http.put(this.tharwaURL + url, params, /*{headers}*/);
  }

  public httpDeleteRequest (url: string): Observable<any> {
    /**
     * add the httpheader  :
     * every put request must have a token
     * **/
    
    const headers =  new HttpHeaders({
      'Accept' : 'application/json'
    });
    return this.http.delete(this.tharwaURL+url, /*{headers}*/);
  }

  public putImage(imgUrl: String, formData): Observable<any>{
      const headers =  new HttpHeaders({
      'Accept' : 'multipart/form-data'
    });
    /** send the put request **/
    return this.http.post(this.tharwaURL + imgUrl, formData, {headers})
  }


  public storeToken(token: string, refresh: string, user_type: string, user_id: string): void{
    this.token = token;
    this.cookieService.set( 'TOKEN', token); 
    this.cookieService.set( 'REFRESH', refresh);
    this.cookieService.set('TYPE',user_type);
    this.cookieService.set('ID', user_id)
  }

  public destroyToken(): void{
    this.cookieService.deleteAll();
  }
}
