import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {BlockDialogComponent} from '../blocking/blocking.component';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef, MatPaginator, MatSnackBar, MatSort, MatTableDataSource} from '@angular/material';
import {ErrorHandling} from '../../../Classes/ErrorHandling';
import {Transfer} from '../../../Classes/Transfer';
import {Account} from '../../../Classes/Account';
import { ValidateTransferService } from '../../../Services/validate-transfer.service';
import { TransfersListService } from '../../../Services/transfers-list.service';
import { RequestHandlerService } from '../../../Services/request-handler.service';

@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.component.html',
  styleUrls: ['./transfer.component.css'],
  providers: [ValidateTransferService, TransfersListService]
})
export class TransferComponent implements OnInit{

  public loading = false;
  public displayedColumns = ['code_sender' , 'code_receiver' , 'date', 'amount', 'bank', 'action'];
  public dataSource: MatTableDataSource<Transfer>;
  public transfers: Transfer[] = [];
  public msg = new ErrorHandling(this.errorMsg);
  public empty = false;
  public path = '';
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    public validateTransfer: ValidateTransferService,
    public transfersList: TransfersListService,
    public  api: RequestHandlerService
  ) { 
  }

  public setPath(){
    this.path = this.api.tharwaURL + this.api.justifUrl;
  }

  public ngOnInit() {
    this.setPath();
    this.initListTransfer();
  }

  public initListTransfer() {
    this.loading = true;
    this.transfersList.getTransfersList().subscribe(
      data => {
        this.transfers = data;
        if (this.transfers.length == 0){
          this.empty = true;  
        } else {
          this.empty = false;  
          // Assign the data to the data source for the table to render
          this.transfers.forEach(transfer => {
            transfer.url_justif = this.path + transfer.url_justif;
            if (transfer.num_acc_sender.toString().length <6){
              let temp = '';
              for (let i = transfer.num_acc_sender.toString().length; i< 6; i++){
                temp += '0' ;
              }
              transfer.num_acc_sender = 'THW' + temp + transfer.num_acc_sender + 'DZD';
            }

            if (transfer.num_acc_receiver.toString().length <6){
              let temp = '';
              for (let i = transfer.num_acc_receiver.toString().length; i< 6; i++){
                temp += '0' ;
              }
              transfer.num_acc_receiver = 'THW' + temp + transfer.num_acc_receiver + 'DZD';
            }
          });
        } 
        this.dataSource = new MatTableDataSource(this.transfers);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public applyFilter(filterValue: string): void{
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public showDiag(id): void{
    const transfer: Transfer = this.getTransfer(id);
    const dialogRef = this.dialog.open(JustificationComponent, {
      width: '50%',
      height: '80%',
      disableClose: true,
      data : transfer
    });
    dialogRef.afterClosed().subscribe(result => {
      const msg = new ErrorHandling(this.errorMsg);
      if (result == 1) {
        this.validateTranfer(transfer.id_justif);
      } else if (result == 0) {
        this.rejectTranfer(transfer.id_justif);
      }
    });
  }

  public validateTranfer(id){
    this.loading = true;
    this.validateTransfer.validateTransfer(id).subscribe(
      data => {
        this.initListTransfer();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public rejectTranfer(id){
    this.loading = true;
    this.validateTransfer.rejectTransfer(id).subscribe(
      data => {
        this.initListTransfer();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public getTransfer(id): Transfer {
    for (let i = 0; i < this.transfers.length; i++) {
      if (this.transfers[i].id_virement == id) {
        return this.transfers[i];
      }
    }
    return null;
  }

  public refresh(): void{
    this.initListTransfer();
  }
}


@Component({
  selector: 'app-justif-dialog',
  templateUrl: 'justifDialog.html',
  styleUrls: ['justifDialog.css']
})

export class JustificationComponent implements  OnInit {

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<BlockDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public transfer: Transfer
  ) {
  }

  public validate(): void{
    this.dialogRef.close(1);
  }

  public reject(): void{
    this.dialogRef.close(0);
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public ngOnInit() {
  }
}
