import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Commission } from '../Classes/Commission';

@Injectable()
export class CommissionsListService {

  private tharwaURL = 'commissions'; /** DNS/commissions to get the commissions' list**/
  
  constructor(private http: RequestHandlerService) { }

  public getCommissionsList(): Observable<Commission[]>{
    /** return the http get request response
     * the commissions' list
     * or an error if it was occured
     * **/
    return this.http.httpGetRequest(this.tharwaURL);
  }

}
