import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';


@Injectable()
export class ChangePasswordService {

  private tharwaURL = 'user/password'; /** DNS/bankers to add a banker**/

  constructor(private http: RequestHandlerService) { }

  public changePassword(oldPassword, newPassword) {
    const params = {
      old_password: oldPassword,
      new_password: newPassword
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL, params);
  }

}
