import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class DeleteBankService {

  private tharwaURL = 'banks/'; /** DNS/banks/{id} to delete the bank with the id : id**/

  constructor(private http: RequestHandlerService) { }

  public deleteBank(id) {
    return this.http.httpDeleteRequest(this.tharwaURL + id);
  }
}
