import { Component, ViewChild, OnInit, Inject} from '@angular/core';
import {MatPaginator, MatSort, MatTableDataSource, MatSnackBar, MatDialogRef, MatDialog, MAT_DIALOG_DATA} from '@angular/material';
import { ErrorHandling } from '../../../Classes/ErrorHandling';
import { Validators, FormControl } from '@angular/forms';
import { Bank } from '../../../Classes/Bank';
import { BanksListService } from '../../../Services/banks-list.service';
import { AddBankService } from '../../../Services/add-bank.service';
import { EditBankService } from '../../../Services/edit-bank.service';
import { DeleteBankService } from '../../../Services/delete-bank.service';
import { InputValidation } from '../../../Classes/InputValidation';


@Component({
  selector: 'app-banks',
  templateUrl: './banks.component.html',
  styleUrls: ['./banks.component.css'],
  providers: [BanksListService, AddBankService, EditBankService, DeleteBankService]
})
export class BanksComponent implements OnInit {

  public displayedColumns = ['code', 'name', 'social_reason', 'address', 'email', 'action'];
  public dataSource: MatTableDataSource<Bank>;
  public banks: Bank[] = [];
  public loading = false;
  public empty = false;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private banksList: BanksListService,
    private addBankService: AddBankService,
    private deleteBankService: DeleteBankService,
    private editBankService: EditBankService,
    public errorMsg: MatSnackBar,
    public dialog: MatDialog
  ) {
  }

  public ngOnInit(): void {
    this.initListBanks();
  }

  public initListBanks(): void {
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.banksList.getBanksList().subscribe(
      data => {
        this.banks = data;
        if (this.banks.length == 0) {
          this.empty = true;
        } else{
          this.empty = false;
        }
        this.dataSource = new MatTableDataSource(this.banks);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
        this.banks.push(new Bank('THR','Tharwa','SARL','Somewhere','tharwabank@gmail.com'));
        this.dataSource = new MatTableDataSource(this.banks);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
      }
    );
  }
  /**
   * Set the paginator and sort after the view init since this component will
   * be able to query its view for the initialized paginator and sort.
   */

  public applyFilter(filterValue: string): void{
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public addBankDialog(): void{
    const dialogRef = this.dialog.open(AddBankDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result!=-1) {
          const bank = {
            code: result.id,
            name : result.name,
            social_reason: result.social_reason,
            address : result.address,
            email: result.email
          };
          this.addBank(bank);
        }
    });

  }

  public addBank(bank): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.addBankService.addBank(bank).subscribe(
      data => {
        this.initListBanks();
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public deleteBankDialog(id): void{
    const dialogRef = this.dialog.open(DeleteBankComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    /** when the dialog is closed show the user a dialog where he can enter the verification code**/
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1) {
        this.deleteBank(id);
      }
    });
  }

  public deleteBank(id): void{
    this.loading = true;
    this.deleteBankService.deleteBank(id).subscribe(
      data => {
        this.initListBanks();
        this.loading = false;
      },
      error => {
        const msg = new ErrorHandling(this.errorMsg);
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public editBankDialog(id): void{
    const dialogRef = this.dialog.open(EditBankDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : this.getBank(id)
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result!=1) {
          const bank = {
            code: result.id,
            name : result.name,
            social_reason: result.social_reason,
            address : result.address,
            email: result.email
          };
          this.editBank(id, bank);
        }
    });
  }

  public editBank(id, bank): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.editBankService.editBank(id, bank).subscribe(
      data => {
        this.initListBanks(); 
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public getBank(id): Bank {
    for (let i = 0; i < this.banks.length; i++) {
      if (this.banks[i].id === id) {
        return this.banks[i];
      }
    }
    return null;
  }

  public refresh(): void{
    this.initListBanks();
  }
}


/** dynamic component : Add bank form dialog **/

@Component({
  selector: 'app-add-bank-dialog',
  templateUrl: 'addBankDialog.html',
  styleUrls: ['addBankDialog.css']
})

export class AddBankDialogComponent implements  OnInit {

  public code = '';

  public idControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z]+/),
    Validators.maxLength(3),
    Validators.minLength(3)
  ]);

  public idMatcher = new InputValidation();

  public nameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);

  public nameMatcher = new InputValidation();
  public socialReasonControl = new FormControl('', [
    Validators.required
  ]);
  public socialReasonMatcher = new InputValidation();
  public addressControl = new FormControl('', [
    Validators.required
  ]);
  public addressMatcher = new InputValidation();
  public emailControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
  ]);
  public emailMatcher = new InputValidation();

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<AddBankDialogComponent>
  ) { }

  public onSubmit(): void{
    const bank = {
      email: this.emailControl.value,
      id: this.idControl.value,
      social_reason: this.socialReasonControl.value,
      name : this.nameControl.value,
      address : this.addressControl.value,
    };
    this.dialogRef.close(bank);
  }

  public onClose(){
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void{}
}


/** dynamic component : edit bank form dialog **/

@Component({
  selector: 'app-edit-bank-dialog',
  templateUrl: 'editBankDialog.html',
  styleUrls: ['editBankDialog.css']
})

export class EditBankDialogComponent implements  OnInit {

  public code = '';

  public idControl = new FormControl('', [
    Validators.required
  ]);

  public idMatcher = new InputValidation();

  public nameControl = new FormControl('', [
    Validators.required
  ]);

  public nameMatcher = new InputValidation();
  public socialReasonControl = new FormControl('', [
    Validators.required
  ]);

  public socialReasonMatcher = new InputValidation();
  public addressControl = new FormControl('', [
    Validators.required
  ]);
  public addressMatcher = new InputValidation();
  public emailControl = new FormControl('', [
    Validators.required,
    Validators.email
  ]);
  public emailMatcher = new InputValidation();

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<EditBankDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public bank: Bank
  ) { }

  public onSubmit(): void {
    let bank : Bank = new Bank(
      this.idControl.value,
      this.nameControl.value,
      this.socialReasonControl.value,
      this.addressControl.value,
      this.emailControl.value);

    if(this.idControl.pristine){
      bank.id = this.bank.id;
    }

    if(this.nameControl.pristine){
      bank.name = this.bank.name;
    }

    if(this.socialReasonControl.pristine){
      bank.social_reason = this.bank.social_reason;
    }

    if(this.addressControl.pristine){
      bank.address = this.bank.address;
    }

    if(this.emailControl.pristine){
      bank.email = this.bank.email;
    }

    this.dialogRef.close(bank);
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void {}
}

/** dynamic component : delete dialog **/
@Component({
  selector: 'app-delete-bank-dialog',
  templateUrl: 'deleteBankDialog.html',
  styleUrls: ['deleteBankDialog.css']
})

export class DeleteBankComponent {

  constructor(
    public dialogRef: MatDialogRef<DeleteBankComponent>
  ) { }

  public delete(): void{
    this.dialogRef.close(1);
  }
}
