import {AfterViewInit, Component, Inject, OnInit, ViewChild} from '@angular/core';
import {Account} from '../../../Classes/Account';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef, MatPaginator, MatSnackBar, MatSort, MatTableDataSource} from '@angular/material';
import {ErrorHandling} from '../../../Classes/ErrorHandling';
import { BlockAccountService } from '../../../Services/block-account.service';
import { AccountListService } from '../../../Services/account-list.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-blocking',
  templateUrl: './blocking.component.html',
  styleUrls: ['./blocking.component.css'],
  providers: [BlockAccountService, AccountListService]
})

export class BlockingComponent implements OnInit, AfterViewInit {
  public loading = false;
  public displayedColumns = ['code', 'amount', 'type', 'status', 'select'];
  public dataSource: MatTableDataSource<Account>;
  public accounts: Account[] = [];
  public msg = new ErrorHandling(this.errorMsg);
  public empty = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    private blockService: BlockAccountService,
    private accountsList: AccountListService,
    private translateService: TranslateService
  ) { }

  public ngOnInit() {
    this.initListAccount();
  }

  public initListAccount() {
    this.loading = true;
    this.accountsList.getAccountsList().subscribe(
      data => {
        this.accounts = data;
        if (this.accounts.length == 0){
          this.empty = true;
        } else {
          this.empty = false;
          this.convertToType();
          this.convertToStatus();
          this.translateService.onLangChange.subscribe(
            data => {
              this.translateType();
              this.translateStatus();
            }
          );
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.accounts);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
      }
    );
  }

  public ngAfterViewInit() {
  }

  public applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public showBlockDiag(id): void{
    const dialogRef = this.dialog.open(BlockDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : this.getAccount(id)
    });
    dialogRef.afterClosed().subscribe(result => {
      const msg = new ErrorHandling(this.errorMsg);
      if (result != -1) {
        this.blockAccount(id, result.object, result.justif);
      }
    });
  }
  
  public blockAccount(id, object, justif){
    this.loading = true;
    this.blockService.blockAccount(id, object, justif).subscribe(
      data => {
        this.initListAccount();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public showDeblockDiag(id): void{
    const dialogRef = this.dialog.open(DeblockDialogComponent, {
      width: '50%',
      height: '95%',
      disableClose: true,
      data : this.getAccount(id)
    });
    
    dialogRef.afterClosed().subscribe(result => {
      const msg = new ErrorHandling(this.errorMsg);
      if (result == 0) {
        this.deblockAccount(id);
      }
    });
  }

  public deblockAccount(id){
    this.loading = true;
    this.blockService.deblockAccount(id).subscribe(
      data => {
        this.initListAccount();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public getAccount(id): Account {
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].id === id) {
        return this.accounts[i];
      }
    }
    return null;
  }

  public canBlock(status):boolean {
    if (status == 'Valid' || status == 'Valide') {
      return true;
    } else if (status == 'Blocked' || status == 'Bloqu')  {
      return false;
    } else {
      return false;
    }
  }

  public convertToType(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].type.toString() == '1') {
        this.translateService.get("TYPEACCOUNT.CURRENT").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '2') {
        this.translateService.get("TYPEACCOUNT.SAVING").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '3') {
        this.translateService.get("TYPEACCOUNT.CUREURO").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '4') {
        this.translateService.get("TYPEACCOUNT.CURDOL").subscribe(
          data => this.accounts[i].type = data
        );
      } else {
        this.translateService.get("TYPEACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].type = data
        );
      }
    }
  }

  public convertToStatus(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].status.toString() == '0') {
        this.translateService.get("STATUSACCOUNT.WAITING").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status.toString() == '1') {
        this.translateService.get("STATUSACCOUNT.VALID").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status.toString() == '2') {
        this.translateService.get("STATUSACCOUNT.VALID").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status.toString() == '3') {
        this.translateService.get("STATUSACCOUNT.BLOCKED").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status.toString() == '4') {
        this.translateService.get("STATUSACCOUNT.DELETED").subscribe(
          data => this.accounts[i].status = data
        );
      } else {
        this.translateService.get("STATUSACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].status = data
        );
      }
    }
  }

  public translateType(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].type == "Current" || this.accounts[i].type == "Courant" ){
        this.translateService.get("TYPEACCOUNT.CURRENT").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Saving" || this.accounts[i].type == "Epargne" ) {
        this.translateService.get("TYPEACCOUNT.SAVING").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Current Euro" || this.accounts[i].type == "Courant euro" ) {
        this.translateService.get("TYPEACCOUNT.CUREURO").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Current Euro" || this.accounts[i].type == "Courant euro" ) {
       this.translateService.get("TYPEACCOUNT.CURDOL").subscribe(
          data => this.accounts[i].type = data
        );
      } else {
        this.translateService.get("TYPEACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].type = data
        );
      }
    }
  }

  public translateStatus(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].status == "En attente" || this.accounts[i].status == "Waiting" ){
        this.translateService.get("STATUSACCOUNT.WAITING").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status == "Valide" || this.accounts[i].status == "Valid" ) {
        this.translateService.get("STATUSACCOUNT.VALID").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status == "Bloqu" || this.accounts[i].status == "Blocked" ) {
        this.translateService.get("STATUSACCOUNT.BLOCKED").subscribe(
          data => this.accounts[i].status = data
        );
      } else if (this.accounts[i].status == "Supprim" || this.accounts[i].status == "Deleted" ) {
       this.translateService.get("STATUSACCOUNT.DELETED").subscribe(
          data => this.accounts[i].status = data
        );
      } else {
        this.translateService.get("STATUSACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].status = data
        );
      }
    }
  }

  public refresh(): void{
    this.initListAccount();
  }
}

/** dynamic component : block account dialog **/
@Component({
  selector: 'app-block-dialog',
  templateUrl: 'blockDialog.html',
  styleUrls: ['blockDialog.css']
})

export class BlockDialogComponent implements  OnInit {

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<BlockDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public account: Account
  ) {
  }


  public block(object, justif): void{
    const result = {
      object : object,
      justif: justif
    }
    this.dialogRef.close(result);
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void{
  }
}


/** dynamic component : deblock account dialog **/
@Component({
  selector: 'app-deblock-dialog',
  templateUrl: 'deblockDialog.html',
  styleUrls: ['deblockDialog.css']
})

export class DeblockDialogComponent implements  OnInit {

  public justif_text = '';
  public justif_img = '';
  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<BlockDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public account: Account
  ) {
  }

  public deblock(): void{
    this.dialogRef.close(0);
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void{
  }
}
