import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class ValidateTransferService {

  private tharwaURL = 'virement/justif/'; /** DNS/transfers to validate a transfer**/

  constructor(private http: RequestHandlerService) { }

  public validateTransfer(id) {
    const params = {
      operation: 1
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

  public rejectTransfer(id) {
    const params = {
      operation: 2
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

}
