import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class EditBankService {

  private tharwaURL = 'banks/'; /** DNS/banks/{id} to update the bank with the id : id**/

  constructor(private http: RequestHandlerService) { }

  public editBank(id, bank) {
    console.log(bank);
    return this.http.httpPutRequest(this.tharwaURL+id, bank);
  }

}
