import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class ValidateAccountService {

  private tharwaURL = 'accounts/'; /** DNS/accounts to validate an account**/

  constructor(private http: RequestHandlerService) { }

  public validateAccount(id) {
    const params = {
      type: 1
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

  public rejectAccount(id) {
    const params = {
      type: 4
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

}
