import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';

@Injectable()
export class AuthorisationService {

  private tharwaURL = 'login/code'; /** DNS/login/code to send code**/
  public access_token = '';

  constructor(private http: RequestHandlerService) { }

  public authorisation(code: string, login: string, mdp: string): Observable<any> {
    /** initialize the POST request paramas**/
    const params = {
      'email' : login,
      'password' : mdp,
      'nonce' : code
    };

    /** return the http post request response**/
    return this.http.httpPostRequest(this.tharwaURL, params);
  }

  /** Initialize the token in constService**/
  public setToken(access_token: string, refresh_token: string, user_type: string,user_id: string) {
    this.access_token = access_token;
    this.http.storeToken(access_token, refresh_token, user_type, user_id);
  }
}
