import { Component, OnInit } from '@angular/core';
import { CommissionsListService } from '../../../Services/commissions-list.service';
import { MatTableDataSource, MatSnackBar, MatDialog } from '@angular/material';
import { Commission } from '../../../Classes/Commission';
import { ErrorHandling } from '../../../Classes/ErrorHandling';

@Component({
  selector: 'app-commissions',
  templateUrl: './commissions.component.html',
  styleUrls: ['./commissions.component.css'],
  providers: [CommissionsListService]
})
export class CommissionsComponent implements OnInit {
  public loading = false;
  public displayedColumns = ['commission', 'amount'];
  public dataSource: MatTableDataSource<Commission>;
  public commissions: Commission[] = [];
  public empty = false;

  constructor(
    public commissionsList: CommissionsListService,
    public errorMsg: MatSnackBar,
    public dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.initListCommissions();
  }

  public initListCommissions() {
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.commissionsList.getCommissionsList().subscribe(
      data => {
        this.commissions = data;
        if (this.commissions.length == 0){
          this.empty = true;
        } else {
            // Assign the data to the data source for the table to render
            this.commissions.forEach(commission => {
              if (commission.valeur == "0"){
                commission.valeur =" free";
              } else {
                if (commission.type  == 1) {
                  commission.valeur += " DA";
                } else  {
                  commission.valeur += "%";
                }
              }
            });
          this.dataSource = new MatTableDataSource(this.commissions);
        }
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }
}
