import { Component, OnInit, ViewChild, AfterViewInit, Inject } from '@angular/core';
import { MatDialog, MatSnackBar, MatDialogRef, MatTableDataSource, MatPaginator, MatSort, MAT_DIALOG_DATA } from '@angular/material';
import { ErrorHandling } from '../../../Classes/ErrorHandling';
import { AccountListService } from '../../../Services/account-list.service';
import { Account } from '../../../Classes/Account';
import { ValidateAccountService } from '../../../Services/validate-account.service';
import { UnvalidAccountsService } from '../../../Services/unvalid-accounts.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-accounts',
  templateUrl: './accounts.component.html',
  styleUrls: ['./accounts.component.css'],
  providers: [UnvalidAccountsService, ValidateAccountService]
})

export class AccountsComponent implements OnInit {
  public loading = false;
  public displayedColumns = ['fullname', 'email', 'type', 'select'];
  public dataSource: MatTableDataSource<Account>;
  public accounts: Account[] = [];
  public msg = new ErrorHandling(this.errorMsg);
  public empty = false;
  public tcourant = "Current" || "Courant";
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    private accountsList: UnvalidAccountsService,
    private validateService: ValidateAccountService,
    private translateService: TranslateService
  ) { }

  public ngOnInit(): void {
    this.initListAccount();
  }

  public initListAccount(): void {
    this.loading = true;
    this.accountsList.getAccountsList().subscribe(
      data => {
        this.accounts = data;
        if (this.accounts.length == 0 ){
          this.empty = true;
        } else {
          this.empty = false;        
          this.convertToType();
          this.translateService.onLangChange.subscribe(
            data => this.translate()
          );
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.accounts);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort; 
        }
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public applyFilter(filterValue: string): void {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public showDiag(id): void{
    const dialogRef = this.dialog.open(AccountDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : this.getAccount(id)
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1) {
        this.validateAccount(id);
      } else if (result == 0) {
        this.rejectAccount(id);
      }
    });
  }

  public validateAccount(id): void{
    this.loading = true;
    this.validateService.validateAccount(id).subscribe(
      data => {
        this.initListAccount();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public rejectAccount(id): void{
    this.loading = true;
    this.validateService.rejectAccount(id).subscribe(
      data => {
        this.initListAccount();
        this.loading = false;
      },
      error => {
        this.msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public getAccount(id): Account {
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].id === id) {
        return this.accounts[i];
      }
    }
    return null;
  }

  public convertToType(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].type.toString() == '1') {
        this.translateService.get("TYPEACCOUNT.CURRENT").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '2') {
        this.translateService.get("TYPEACCOUNT.SAVING").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '3') {
        this.translateService.get("TYPEACCOUNT.CUREURO").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type.toString() == '4') {
        this.translateService.get("TYPEACCOUNT.CURDOL").subscribe(
          data => this.accounts[i].type = data
        );
      } else {
        this.translateService.get("TYPEACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].type = data
        );
      }
    }
  }

  public translate(){
    for (let i = 0; i < this.accounts.length; i++) {
      if (this.accounts[i].type == "Current" || this.accounts[i].type == "Courant" ){
        this.translateService.get("TYPEACCOUNT.CURRENT").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Saving" || this.accounts[i].type == "Epargne" ) {
        this.translateService.get("TYPEACCOUNT.SAVING").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Current Euro" || this.accounts[i].type == "Courant euro" ) {
        this.translateService.get("TYPEACCOUNT.CUREURO").subscribe(
          data => this.accounts[i].type = data
        );
      } else if (this.accounts[i].type == "Current Euro" || this.accounts[i].type == "Courant euro" ) {
       this.translateService.get("TYPEACCOUNT.CURDOL").subscribe(
          data => this.accounts[i].type = data
        );
      } else {
        this.translateService.get("TYPEACCOUNT.UNKOWN").subscribe(
          data => this.accounts[i].type = data
        );
      }
    }
  }

  public refresh(): void{
    this.initListAccount();
  }
}


/** dynamic component : validate account dialog **/
@Component({
  selector: 'app-account-dialog',
  templateUrl: 'accountDialog.html',
  styleUrls: ['accountDialog.css']
})

export class AccountDialogComponent implements  OnInit {

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<AccountDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public account: Account
  ) {
  }

  public reject(): void {
    this.dialogRef.close(0);
  }

  public validate(): void {
    this.dialogRef.close(1);
  }

  public onClose(): void {
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void {
  }
}
