import { Component, OnInit, Inject } from '@angular/core';
import { Banker } from '../../../Classes/Banker';
import { BankerProfileService } from '../../../Services/banker-profile.service';
import { ActivatedRoute } from '@angular/router';
import { EditProfileService } from '../../../Services/edit-profile.service';
import { MatSnackBar, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ErrorHandling } from '../../../Classes/ErrorHandling';
import { FormControl, Validators } from '@angular/forms';
import { InputValidation } from '../../../Classes/InputValidation';
import { ChangePasswordService } from '../../../Services/change-password.service';
import { BankerService } from '../../../Services/banker.service';
import { RequestHandlerService } from '../../../Services/request-handler.service';

@Component({
  selector: 'app-banker-profile',
  templateUrl: './bankerProfile.component.html',
  styleUrls: ['./bankerProfile.component.css'],
  providers: [BankerService, EditProfileService, ChangePasswordService]
})
export class BankerProfileComponent implements OnInit {
  public loading = false;
  public disable = true;
  public banker: Banker = new Banker(0,'','','','','','');
  public path = "";
  constructor(
    public errorMsg: MatSnackBar,
    private route: ActivatedRoute,
    private profile: BankerService,
    private pwdService: ChangePasswordService,
    public dialog: MatDialog,
    public editProfileService: EditProfileService,
    public api: RequestHandlerService
  ) { 
    this.path = this.api.imgUrl;
  }

  ngOnInit(): void{
    const id = +this.route.parent.snapshot.paramMap.get('id');
    this.initProfile(id);
  }

  public initProfile(id): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.profile.getBanker(id,1).subscribe(
      data => {
        this.loading = false;
        this.banker = data;
        this.banker.photo = this.api.tharwaURL + this.path + this.banker.photo;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public editProfileDialog(): void {
    const dialogRef = this.dialog.open(EditBankerProfileComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : this.banker
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result!=-1) {
          this.editProfile(result)
        }
    });
  }

  public editProfile(banker): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.editProfileService.editBanker(banker, this.banker.id).subscribe(
      data => {
        if (banker.photo) {
          this.editProfileService.postImage(banker.photo, this.banker.id).subscribe(
            response => {
              this.initProfile(this.banker.id);
              this.loading = false;
            }, error => {
              msg.handleError(error);
              this.loading = false;
            }
          );
        }
        this.loading = false;
        this.banker = banker;
        this.banker.photo = this.api.tharwaURL + this.path + this.banker.photo;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      })
  }

  public changePasswordDialog(): void{
    const dialogRef = this.dialog.open(ChangeBankerPasswordComponent, {
      width: '40rem',
      height: 'auto',
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result!=-1) {
          this.changePassword(result.oldPassword, result.newPassword);
        }
    });
  }

  public changePassword(oldPassword, newPassword): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.pwdService.changePassword(oldPassword, newPassword).subscribe(
      data => {
        msg.showErrorMsg('Password has successfully changed', 200)
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      });
  }
}

/** dynamic component : edit banker form dialog **/

@Component({
  selector: 'app-edit-banker-profile',
  templateUrl: 'editBankerProfile.html',
  styleUrls: ['editBankerProfile.css']
})


export class EditBankerProfileComponent {
  public hide = true;
  imagefile:  File;
  filePreviewPath = '';

  nameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);
  nameMatcher = new InputValidation();

  emailControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
  ]);
  emailMatcher = new InputValidation();

  phoneControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[0-9]*/),
    Validators.maxLength(10),
    Validators.minLength(10)
  ]);
  phoneMatcher = new InputValidation();

  addressControl = new FormControl('', [
    Validators.required
  ]);
  addressMatcher = new InputValidation();

  constructor(
    public dialogRef: MatDialogRef<EditBankerProfileComponent>,
    @Inject(MAT_DIALOG_DATA) public banker: Banker
  ) {
    this.filePreviewPath = this.banker.photo;
  }

  onSubmit() {
    const banker = {
      id: this.banker.id,
      name : this.nameControl.value,
      email: this.emailControl.value,
      phone_number: this.phoneControl.value,
      address : this.addressControl.value,
      photo: this.imagefile
    };
    if (this.nameControl.pristine) {
      banker.name = this.banker.name;
    }
    if (this.emailControl.pristine) {
      banker.email = this.banker.email;
    }
    if (this.phoneControl.pristine) {
      banker.phone_number = this.banker.phone_number;
    }
    if (this.addressControl.pristine) {
      banker.address = this.banker.address;
    }
    this.dialogRef.close(banker);
  }

  addImg(event: any) {
    this.imagefile = event.target.files.item(0);
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (event: any) => {
          this.filePreviewPath = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
   }
  }


   isImage(file: File): boolean {
    return /^image\//.test(file.type);
   }

  removeImage(event: any) {
    this.imagefile = null;
  }

  formatSize(bytes) {
    if (bytes === 0) {
      return '0 B';
    }
    const k = 1000,
      dm = 3,
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

}


/** dynamic component : change password dialog **/

@Component({
  selector: 'app-change-password',
  templateUrl: 'passwordBanker.html',
  styleUrls: ['passwordBanker.css']
})


export class ChangeBankerPasswordComponent {
  public hideold = true;
  public hidenew = true;
  public hideconf = true;

  oldPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherOldPassword = new InputValidation();

  confPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherConfPassword = new InputValidation();

  newPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherNewPassword = new InputValidation();
  constructor(
    public dialogRef: MatDialogRef<ChangeBankerPasswordComponent>,
    public errorMsg: MatSnackBar,
  ){}

  public showNewPassword(): void {
    this.hidenew = !this.hidenew;
  }

  public showOldPassword(): void {
    this.hideold = !this.hideold;
  }
  
  public showConfPassword(): void {
    this.hideconf = !this.hideconf;
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public validate(): void{
    if(this.newPasswordFormControl.value == this.confPasswordFormControl.value){
      const response = {
        oldPassword: this.oldPasswordFormControl.value,
        newPassword: this.newPasswordFormControl.value
      }
      this.dialogRef.close(response);
    } else {
      const msg = new ErrorHandling(this.errorMsg);
      msg.showErrorMsg('Passwords dont match', '=/=');
    }
  }
}