import { Injectable, Injector } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { RequestHandlerService } from './request-handler.service';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map } from 'rxjs/operators';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor( 
    private http: HttpClient,
    private auth: RequestHandlerService,
    private inj: Injector,
    private router: Router,
    private cookieService: CookieService
  ) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    request = request.clone({
      setHeaders: {
        'Authorization': this.auth.token
      }
    });
    
    return next.handle(request)
      .pipe(
        map(res => res),
        catchError((err: HttpErrorResponse) => {
          if (err.status === 401){
            this.handle401();
          }
          //return new Observable<HttpEvent<any>>();
          return next.handle(request);
        })
      )
  }

  public handle401(): void{
    this.refrechToken().subscribe(
      response => {
        let authToken: string = response['access_token'];
        let refreshToken: string = response['refresh_token'];
        this.cookieService.set('TOKEN',authToken);
        this.cookieService.set('REFRESH', refreshToken);
        this.auth.token = authToken
      }, 
      error => {
        //logout users
        this.auth.destroyToken();
        //redirect to login
        this.router.navigate(['/home']);
      });
  }

  public refrechToken() {
    let refreshAuth = this.cookieService.get('REFRESH');
    let url: string = this.auth.tharwaURL + 'auth/refresh';

    return this.http.get(url, {
      headers: new HttpHeaders().set('refreshAuthorization', refreshAuth)
    });
  }
}