import { ActivatedRoute, Router } from '@angular/router';
import { ChangeDetectorRef } from '@angular/core';
import { Component } from '@angular/core';
import { ErrorHandling } from '../../Classes/ErrorHandling';
import { ManagerService } from '../../Services/manager.service';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatSnackBar, MatDialog, MatDialogRef } from '@angular/material';
import { OnInit } from '@angular/core';
import { OnDestroy } from '@angular/core';
import { User } from '../../Classes/user';
import { RequestHandlerService } from '../../Services/request-handler.service';
import { LogoutService } from '../../Services/logout.service';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'app-gestionnaire',
  templateUrl: './manager.component.html',
  styleUrls: ['./manager.component.css'],
  providers : [ManagerService, LogoutService]
})
export class ManagerComponent implements OnInit, OnDestroy  {

  public mobileQuery: MediaQueryList;
  public language = "Franais";
  public select = [
    {id: 1, name: 'Dashboard', selected: true, route: 'dashboard', icon : 'dashboard'},
    {id: 2, name: 'Commission', selected: false, route: 'commissions', icon : 'monetization_on'},
    {id: 3, name: 'Banks', selected: false, route: 'banks', icon : 'account_balance'},
    {id: 4, name: 'Bankers', selected: false, route: 'bankers', icon : 'supervisor_account'},
    {id: 5, name: 'Profile', selected: false, route: 'profile', icon : 'perm_identity'},
    {id: 6, name: 'Logout', selected: false, route: 'dashboard', icon : 'highlight_off'}];
  private _mobileQueryListener: () => void;

  public manager: User = new User(0, '','', '');
  public path = '';

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    private route: ActivatedRoute,
    private managerService: ManagerService,
    public logoutService: LogoutService,
    public apiService: RequestHandlerService,
    private redirect: Router,
    public translate: TranslateService,
  ) {
      this.path = this.apiService.imgUrl;
    /*
    **we detect the screen size changes : if size > 600px than mobileQuery.matchs = true,
    **else mobileQuery.matchs = false
    */
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    this.initNavItems();
  }

  ngOnInit(): void {
    /** retrieve the manager id from the route**/
    const id = +this.route.snapshot.paramMap.get('id');
    this.getManagerInfo(id);
  }

  getManagerInfo(id) {
     /** send the request GET /manager/{id} to get the manager's info **/
     this.managerService.getManager(id).subscribe(
      data => {
        this.manager = data;
        this.manager.photo = this.apiService.tharwaURL + this.path + this.manager.photo;
      },
      error => {
        /** show error**/
        const msg = new ErrorHandling(this.errorMsg);
        msg.handleError(error);
      }
    );
  }

  ngOnDestroy(): void {
    /** once the component is destroyed, remove the listner**/
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  selected(id) {
    for (let i = 0; i < this.select.length; i++) {
      if (this.select[i].id == id) {
        this.select[i].selected = true;
      } else {
        this.select[i].selected = false;
      }
    }
    if (id == 6) {
      this.showLogoutDiag();
    }
  }

  public showLogoutDiag() {
    const dialogRef = this.dialog.open(LogoutComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    /** when the dialog is closed show the user a dialog where he can enter the verification code**/
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1) {
        this.logoutService.logout().subscribe(
          data => {
            this.redirect.navigate(['/home']);
            this.apiService.destroyToken();
          },
          error => {
            const msg = new ErrorHandling(this.errorMsg);
            msg.handleError(error);
          }
        );
      }
    });
  }

  public initNavItems(): void{
    this.translate.get("MANAGERNAV.DASHBOARD").subscribe(
      data => this.select[0].name = data
    );
    this.translate.get("MANAGERNAV.COMMISSION").subscribe(
      data => this.select[1].name = data
    );
    this.translate.get("MANAGERNAV.BANKS").subscribe(
      data => this.select[2].name = data
    );
    this.translate.get("MANAGERNAV.BANKERS").subscribe(
      data => this.select[3].name = data
    );
    this.translate.get("MANAGERNAV.PROFILE").subscribe(
      data => this.select[4].name = data
    );
    this.translate.get("MANAGERNAV.LOGOUT").subscribe(
      data => this.select[5].name = data
    );
  }

  public switchLanguage(): void{
    if (this.language == 'Franais'){
      this.translate.use('en');
      this.language = 'English';
    } else {
      this.translate.use('fr');
      this.language = 'Franais';
    }
    this.initNavItems();
  }
}


/** dynamic component : logout dialog **/
@Component({
  selector: 'app-logout-dialog',
  templateUrl: 'logout.html',
  styleUrls: ['logout.css']
})

export class LogoutComponent {
  constructor(
    public dialogRef: MatDialogRef<LogoutComponent>
  ) { }

  logout() {
    this.dialogRef.close(true);
  }
}
