import { Component, ViewChild, OnInit, AfterViewInit} from '@angular/core';
import {MatPaginator, MatSort, MatTableDataSource, MatSnackBar, MatDialogRef, MatDialog} from '@angular/material';
import { BankersListService } from '../../../Services/bankers-list.service';
import { ErrorHandling } from '../../../Classes/ErrorHandling';
import { AddBankerService } from '../../../Services/add-banker.service';
import { Validators, FormControl } from '@angular/forms';
import { Banker } from '../../../Classes/Banker';
import { RequestHandlerService } from '../../../Services/request-handler.service';
import { DeleteBankerService } from '../../../Services/delete-banker.service';
import {InputValidation} from '../../../Classes/InputValidation';

@Component({
  selector: 'app-bankers-list',
  templateUrl: './bankers-list.component.html',
  styleUrls: ['./bankers-list.component.css'],
  providers: [BankersListService, AddBankerService, DeleteBankerService]
})
export class BankersListComponent implements OnInit, AfterViewInit {
  public loading = false;
  public displayedColumns = ['photo', 'fullname', 'address', 'phone', 'email', 'action'];
  public dataSource: MatTableDataSource<Banker>;
  public users: Banker[] = [];
  public path = '';
  public generalInfo = 'General Information';
  public contactInfo = 'Contact Information';
  public profile = 'profile';
  public empty = false;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private bankersList: BankersListService,
    private addBankerService: AddBankerService,
    private deleteBankerService: DeleteBankerService,
    public errorMsg: MatSnackBar,
    public dialog: MatDialog,
    private apiService: RequestHandlerService
  ) {
     this.path = this.apiService.imgUrl;
  }

  public ngOnInit(): void {
    this.initListBanker();
  }

  public initListBanker(): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.bankersList.getBankersList().subscribe(
      data => {
        this.users = data;
        if (this.users.length == 0) {
          this.empty = true;
        } else {
          for (let i = 0; i < this.users.length; i++) {
            this.users[i].photo = this.apiService.tharwaURL + 'images/customer_min/' + this.users[i].photo;
          }
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.users);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }
  /**
   * Set the paginator and sort after the view init since this component will
   * be able to query its view for the initialized paginator and sort.
   */

  public ngAfterViewInit() {
  }

  public applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public addBankerDialog(): void{
    const dialogRef = this.dialog.open(BankerDialogComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result!=-1) {
        this.loading = true;
        const banker = {
          email: result.email,
          password: result.password,
          phone_number: result.phone_number,
          name : result.name,
          firstname : result.firstname,
          address : result.address
        };
        this.addBanker(banker, result.photo);
      }
    });
  }

  public addBanker(banker, photo): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.addBankerService.addBanker(banker).subscribe(
      data => {
        const id = data.user_id;
        this.addBankerService.postImage(photo, id).subscribe(
          resp => {
            this.loading = false;
            this.initListBanker();
          },
          error => {
            msg.handleError(error);
            this.loading = false;
            this.initListBanker();
          }
        );
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public deleteBankerDialog(id): void{
    const dialogRef = this.dialog.open(DeleteBankerComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    /** when the dialog is closed show the user a dialog where he can enter the verification code**/
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1) {
        this.deleteBanker(id)
      }
    });
  }

  public deleteBanker(id): void{
    let loading = true;
    this.deleteBankerService.deleteBanker(id).subscribe(
      data => {
        this.initListBanker();
      },
      error => {
        const msg = new ErrorHandling(this.errorMsg);
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public refresh(): void{
    this.initListBanker();
  }
}


/** dynamic component : Add banker form dialog **/
@Component({
  selector: 'app-add-banker-dialog',
  templateUrl: 'addBankerDialog.html',
  styleUrls: ['addBankerDialog.css']
})

export class BankerDialogComponent implements  OnInit {
  public hide = true;
  public imagefile:  File;
  public generalInfo = 'Informations Generales';
  public contactInfo = 'Informations de contacts';
  public profile = 'profile';
  public nameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);
  public nameMatcher = new InputValidation();
  public firstnameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);
  public firstnameMatcher = new InputValidation();
  public passwordControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,12}$/)
  ]);
  public matcherPassword = new InputValidation();
  public addressControl = new FormControl('', [
    Validators.required
  ]);
  public addressMatcher = new InputValidation();
  public phoneControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[0-9]*/),
    Validators.minLength(10),
    Validators.maxLength(10)
  ]);
  public phoneMatcher = new InputValidation();

  public emailControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
  ]);
  public emailMatcher = new InputValidation();
  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<BankerDialogComponent>
  ) { }

  public onSubmit(File): void{
    const banker = {
      email: this.emailControl.value,
      password: this.passwordControl.value,
      phone_number: this.phoneControl.value,
      name : this.nameControl.value,
      firstname : this.firstnameControl.value,
      address : this.addressControl.value,
      photo: this.imagefile
    };
    this.dialogRef.close(banker);
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public showPassword(): void{
    this.hide = !this.hide;
  }
  public ajouterImageCatalogue(event: any) {
    this.imagefile = event.target.files.item(0);
  }

  public isImage(file: File): boolean {
    return /^image\//.test(file.type);
  }

  public removeImage(event: any): void{
    this.imagefile = null;
  }

  public formatSize(bytes): string{
    if (bytes == 0) {
      return '0 B';
    }
    const k = 1000,
      dm = 3,
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  public ngOnInit(): void {}
}


/** dynamic component : delete dialog **/
@Component({
  selector: 'app-delete-banker-dialog',
  templateUrl: 'deleteBankerDialog.html',
  styleUrls: ['deleteBankerDialog.css']
})

export class DeleteBankerComponent {
  constructor(
    public dialogRef: MatDialogRef<DeleteBankerComponent>
  ) { }

  public delete(): void{
    this.dialogRef.close(true);
  }
}
