import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
@Injectable()
export class AddBankService {

  private tharwaURL = 'banks'; /** DNS/banks to add a bank**/

  constructor(private http: RequestHandlerService) { }

  public addBank(bank) {
   /** return the http post request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPostRequest(this.tharwaURL, bank);
  }
}
