import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef, MatPaginator, MatSnackBar, MatSort, MatTableDataSource} from '@angular/material';
import {Transfer} from '../../../Classes/Transfer';
import {ErrorHandling} from '../../../Classes/ErrorHandling';

import {OrderTransfer} from '../../../Classes/OrderTransfer';

@Component({
  selector: 'app-transfer-order',
  templateUrl: './transfer-order.component.html',
  styleUrls: ['./transfer-order.component.css'],
  providers: []
})
export class TransferOrderComponent implements OnInit {

  public loading = false;
  public displayedColumns = ['employer_code' , 'employer_name' , 'date', 'nbr', 'action'];
  public dataSource: MatTableDataSource<OrderTransfer>;
  public transfers: OrderTransfer[] = [];
  public empty = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    public errorMsg: MatSnackBar
  ) { }

  public ngOnInit(): void{
    this.initListTransfers();
  }

  public initListTransfers(): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    /*this.transfersOrderList.getAccountsList().subscribe(
      data => {
        this.transfers = data;
        if (this.transfers.length == 0 ){
          this.empty = true;
        } else {
          // Assign the data to the data source for the table to render
          this.dataSource = new MatTableDataSource(this.transfers);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        }
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );*/
    this.transfers.push(
      new OrderTransfer(
        1,
        'THW00001DA',
        'Senouci Nur El Imane',
        '12/12/2018',
        3
      ),
      new OrderTransfer(
        2,
        'THW00002DA',
        'Djaoud Kawthar',
        '15/02/2018',
        2)
    );
    this.dataSource = new MatTableDataSource<OrderTransfer>(this.transfers);
  }

  public applyFilter(filterValue: string): void{
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public showDiag(id): void{
    const dialogRef = this.dialog.open(JustifOrderComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : id
    });
    this.loading = true;
    dialogRef.afterClosed().subscribe(result => {
      const msg = new ErrorHandling(this.errorMsg);
      if (result !== -1) {
      } else {
        msg.showErrorMsg('Error when getting result', 0);
        this.loading = false;
      }
    });
  }

  public refresh(): void{
    this.initListTransfers();
  }
}


@Component({
  selector: 'app-order-justif-dialog',
  templateUrl: 'justifOrderDialog.html',
  styleUrls: ['justifOrderDialog.css']
})

export class JustifOrderComponent implements  OnInit {

  public displayedColumns = ['id', 'code_receiver' , 'amount', 'bank'];
  public dataSource: MatTableDataSource<Transfer>;
  public transfers: Transfer[] = [];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<JustifOrderComponent>,
    @Inject(MAT_DIALOG_DATA) public id: number
  ) {
  }

  public validate(): void {
    this.dialogRef.close(1);
  }

  public reject(): void{
    this.dialogRef.close(0);
  }

  public close(): void{
    this.dialogRef.close(-1);
  }

  public ngOnInit(): void{
  }
}
