import { Injectable } from '@angular/core';
import { RequestHandlerService } from './request-handler.service';
import { Observable } from 'rxjs/internal/Observable';
import { User } from '../Classes/user';

@Injectable()
export class BankerService {

  private tharwaURL = 'bankers/'; /** DNS/bankers/{id} to get the banker with the id : id**/

  constructor(private http: RequestHandlerService) { }

  public getBanker(id, option): Observable<any>{
    /** return the http get request response
     * the banker info
     * or an error if it was occured
     * **/
    return this.http.httpGetRequest(this.tharwaURL + id+"/"+option);
  }

}
