import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class DeleteBankerService {

  private tharwaURL = 'bankers/'; /** DNS/bankers/{id} to delete the banker with the id : id**/

  constructor(private http: RequestHandlerService) { }

  public deleteBanker(id) {
    return this.http.httpDeleteRequest(this.tharwaURL + id);
  }
}
