import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Transfer } from '../Classes/Transfer';

@Injectable()
export class TransfersListService {

  private tharwaURL = 'virements_internes'; /** DNS/virements_internes to get the unvalid accounts' list**/

  constructor(private http: RequestHandlerService) { }

  public getTransfersList(): Observable<Transfer[]>{
    /** return the http get request response
     * the accounts' list
     * or an error if it was occured
     * **/
    return this.http.httpGetRequest(this.tharwaURL);
  }
}
