import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';

@Injectable()
export class BlockAccountService {

  private tharwaURL = 'accounts/'; /** DNS/bankers to add a banker**/

  constructor(private http: RequestHandlerService) { }

  public blockAccount(id, object, justif) {
    const params = {
      type: 3,
      justif_object: object,
      justif: justif
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

  public deblockAccount(id) {
    const params = {
      type: 2
    };
   /** return the http put request response
     * 201
     * or an error if it was occured
     * **/
    return this.http.httpPutRequest(this.tharwaURL + id, params);
  }

}
