import {Component, OnInit} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CookieService } from 'ngx-cookie-service';
import { Router } from '@angular/router';
import { AuthorisationService } from './Services/authorisation.service';
import { UserIdleService } from 'angular-user-idle';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  constructor(
    private authorisation: AuthorisationService,
    private cookieService: CookieService,
    private router: Router,
    public translate: TranslateService,
    private userIdle: UserIdleService
  ) {
    // this language will be used as a fallback when a translation isn't found in the current language
    translate.setDefaultLang('en');

    // the lang to use, if the lang isn't available, it will use the current loader to get them
   translate.use('fr');
  }

  public ngOnInit(): void{
    this.handleIdle();
    this.handleToken();
  }

  public handleIdle(): void{
    //Start watching for user inactivity.
    this.userIdle.startWatching();
    
    // Start watching when user idle is starting.
    this.userIdle.onTimerStart().subscribe(count => console.log(count));
        
    // Start watch when time is up.
    this.userIdle.onTimeout().subscribe(() =>{
      this.cookieService.deleteAll();
      this.router.navigate(['/home']);
    });    
  }

  public handleToken():  void{
    const tokenExists: boolean = this.cookieService.check('TOKEN');
    if (tokenExists) {
      const access_token = this.cookieService.get("TOKEN");
      const refresh_token = this.cookieService.get("REFRESH");
      const user_type = this.cookieService.get("TYPE");
      const user_id = this.cookieService.get("ID");

      this.authorisation.setToken(access_token, refresh_token, user_type, user_id);
      if (user_type == '2') {
        this.router.navigate(['/manager/' + user_id + '/dashboard']);
      } else if (user_type == '1') {
        this.router.navigate(['/banker/' + user_id + '/home']);
      }
    }
  }

  stop() {
    this.userIdle.stopTimer();
  }
 
  stopWatching() {
    this.userIdle.stopWatching();
  }
 
  startWatching() {
    this.userIdle.startWatching();
  }
 
  restart() {
    this.userIdle.resetTimer();
  }
}
