import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { User } from '../../Classes/user';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatSnackBar, MatDialog, MatDialogRef } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { BankerService } from '../../Services/banker.service';
import { ErrorHandling } from '../../Classes/ErrorHandling';
import { RequestHandlerService } from '../../Services/request-handler.service';
import { LogoutService } from '../../Services/logout.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-banker',
  templateUrl: './banker.component.html',
  styleUrls: ['./banker.component.css'],
  providers: [BankerService, LogoutService]

})
export class BankerComponent implements OnInit, OnDestroy {

  public language = "Franais";
  public mobileQuery: MediaQueryList;
  public items = [
    {id: 1, name: 'Acceuil', selected: true, route: 'home', icon : 'home', sub_items: [], notif: 0},
    {id: 2, name: 'Validation', selected: false, route: 'accounts', icon : 'done',
      sub_items: [
        {id: 8, name: 'Comptes', selected: true, route: 'accounts', icon : 'supervisor_account',notif: 3},
        {id: 9, name: 'Virements', selected: false, route: 'transfers', icon : 'compare_arrows',notif: 2},
        {id: 10, name: 'Ordres de virements', selected: false, route: 'transfersOrder', icon : 'description',notif: 10}
    ], notif: 15},
    {id: 3, name: 'Virements Externes', selected: false, route: 'transfersExtern', icon : 'list', sub_items: [], notif: 20},
    {id: 4, name: '(D)Bloquage', selected: false, route: 'blocking', icon : 'lock', sub_items: [], notif: 12},
    {id: 5, name: 'Profil', selected: false, route: 'profile', icon : 'perm_identity', sub_items: [], notif: 0},
    {id: 6, name: 'Dconnexion', selected: false, route: 'home', icon : 'highlight_off', sub_items: [], notif: 0} ];


  private _mobileQueryListener: () => void;

  public banker: User = new User(0, '','', '');
  public path = '';
  public validation = false;


  constructor(
    public changeDetectorRef: ChangeDetectorRef,
    public media: MediaMatcher,
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    public route: ActivatedRoute,
    public bankerService: BankerService,
    public logoutService: LogoutService,
    public apiService: RequestHandlerService,
    public redirect: Router,
    public translate: TranslateService
  ) {
    this.path = this.apiService.tharwaURL + this.apiService.imgUrl;

    /*
    **we detect the screen size changes : if size > 600px than mobileQuery.matchs = true,
    **else mobileQuery.matchs = false
    */
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  public ngOnInit(): void {
    /** retrieve the manager id from the route**/
    const id = +this.route.snapshot.paramMap.get('id');
    this.getBankerInfo(id);
  }

  public getBankerInfo(id): void{
     /** send the request GET /manager/{id} to get the manager's info **/
     this.bankerService.getBanker(id,0).subscribe(
      data => {
        this.banker = data;
        this.banker.photo = this.path + this.banker.photo;
      },
      error => {
        /** show error**/
        const msg = new ErrorHandling(this.errorMsg);
        msg.handleError(error);
      }
    );
  }

  public ngOnDestroy(): void {
    /** once the component is destroyed, remove the listner**/
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  public selected(id): void{
    for (let i = 0; i < this.items.length; i++) {
      this.items[i].selected = this.items[i].id == id;
    }
    if (id == 2) {
      this.validation = !this.validation;
      if (this.validation){
        this.items[1].icon = 'keyboard_arrow_down';
      } else {
        this.items[1].icon = 'done';
      }
    }
    if (id == 6) {
      this.showLogoutDiag();
    }
  }

  public subselected(id): void{
    for (let i = 0; i < this.items[1].sub_items.length; i++) {
      this.items[1].sub_items[i].selected = this.items[1].sub_items[i].id == id;
      for (let i = 0; i < this.items.length; i++) {
        this.items[i].selected = false;
      } 
      this.items[1].selected = true;
    }
  }

  public showLogoutDiag(): void{
    const dialogRef = this.dialog.open(BankerLogoutComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true
    });

    /** when the dialog is closed show the user a dialog where he can enter the verification code**/
    dialogRef.afterClosed().subscribe(result => {
      if (result == 1) {
        this.logoutService.logout().subscribe(
          data => {
            this.redirect.navigate(['/home']);
            this.apiService.destroyToken();
          },
          error => {
            const msg = new ErrorHandling(this.errorMsg);
            msg.handleError(error);
          }
        );
      }
    });
  }

  public switchLanguage(){
    if (this.language == 'Franais'){
      this.translate.use('en');
      this.language = 'English';
      this.translateToEn();
    } else {
      this.translate.use('fr');
      this.language = 'Franais';
      this.translateToFr();
    }
  }

  public translateToEn(){
    this.items[0].name = 'Home';
    this.items[1].sub_items[0].name = 'Accounts';
    this.items[1].sub_items[1].name = 'Transfers';
    this.items[1].sub_items[2].name = 'Transfers Order';
    this.items[2].name = 'Extern Transfers';
    this.items[3].name = '(De)Blocking';
    this.items[4].name = 'Profile';
    this.items[5].name = 'Logout';
  }

  public translateToFr(){
    this.items[0].name = 'Acceuil';
    this.items[1].sub_items[0].name = 'Comptes';
    this.items[1].sub_items[1].name = 'Virements';
    this.items[1].sub_items[2].name = 'Ordres de virements';
    this.items[2].name = 'Virements Externes';
    this.items[3].name = '(D)Bloquage';
    this.items[4].name = 'Profil';
    this.items[5].name = 'Dconnexion';
  }
}


/** dynamic component : logout dialog **/
@Component({
  selector: 'app-logout-dialog',
  templateUrl: 'logout.html',
  styleUrls: ['logout.css']
})

export class BankerLogoutComponent {

  constructor(
    public dialogRef: MatDialogRef<BankerLogoutComponent>
  ) { }

  public logout(): void{
    this.dialogRef.close(true);
  }
}
