import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Stat } from '../../../../Classes/Stat';
import { Chart } from 'chart.js';

@Component({
  selector: 'app-stat-commissions',
  templateUrl: './stat-commissions.component.html',
  styleUrls: ['./stat-commissions.component.css']
})
export class StatCommissionsComponent implements OnInit {
  public stats: Stat[] = [
    new Stat(4000, 'Commmissions today', '#ff4444', 'today' ),
    new Stat(4000, 'Commmissions this month', '#ffbb33', 'date_range'),
    new Stat(4000, 'Commmissions this quarter', '#2BBBAD', 'pie_chart'),
    new Stat(4000, 'Commmissions this year', '#33b5e5', 'timeline')
  ]

  public chart = [];
  @ViewChild('canvas') canvas;
  
  constructor(
    private elementRef: ElementRef
  ) { }

  ngOnInit() {
    this.showWeekChart();
    this.showMonthChart();
    this.showYearChart();
    this.showQuarterChart();
  }


  public showWeekChart(): void{
    const labels = ['dim', 'lun','mar','mer','jeu','ven','sam'];
    const data= [2000, 4000, 6000, 8000, 100000, 4000, 3000];
    let week = this.elementRef.nativeElement.querySelector('#week');
    this.chart = new Chart(week, {
      type: 'line',
      data: {
        labels: labels,
        datasets: [
          { 
            data: data,
            borderColor: "#ff4444",
            backgroundColor: "#ffffff",
            fill: false
          }
        ]
      },
      options: {
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true
          }],
        }
      }
    });
  }

  public showMonthChart(): void{
    const labels = ['jan', 'feb','mar','avril','mai','juin','juillet','aout', 'sept', 'oct', 'nov','dec'];
    const data= [2000, 4000, 6000, 8000, 100000, 4000, 3000, 2000, 4000, 6000, 8000, 100000];
    let month = this.elementRef.nativeElement.querySelector('#month');
    this.chart = new Chart(month, {
      type: 'line',
      data: {
        labels: labels,
        datasets: [
          { 
            data: data,
            borderColor: "#ffbb33",
            backgroundColor: "#ffffff",
            fill: false
          }
        ]
      },
      options: {
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true
          }],
        }
      }
    });
  }

  public showYearChart(): void{
    const labels = ['2017','2018'];
    const data= [2000, 4000];
    let year = this.elementRef.nativeElement.querySelector('#year');
    this.chart = new Chart(year, {
      type: 'bar',
      data: {
        labels: labels,
        datasets: [
          { 
            data: data,
            borderColor: "#2BBBAD",
            backgroundColor: "#3cba9f",
            fill: true
          }
        ]
      },
      options: {
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true,
            ticks: {
              beginAtZero: true
            }
          }],
        }
      }
    });
  }

  public showQuarterChart(): void{
    const labels = ['Q1','Q2','Q3','Q4'];
    const data= [2000, 4000,10000, 1000];
    let quarter = this.elementRef.nativeElement.querySelector('#quarter');
    this.chart = new Chart(quarter, {
      type: 'doughnut',
      data: {
        labels: labels,
        datasets: [
          { 
            data: data,
            borderColor: '#fff',
            backgroundColor: [
              '#ff4444',
              '#ffbb33',
              '#2BBBAD',
              '#33b5e5'
              ],
            fill: true
          }
        ]
      },
      options: {
        legend: {
          display: true
        },
        scales: {
          xAxes: [{
            display: false
          }],
          yAxes: [{
            display: false
          }],
        }
      }
    });
  }
}
