import {Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MatPaginator, MatSnackBar, MatSort, MatTableDataSource} from '@angular/material';
import {Transfer} from '../../../Classes/Transfer';
import {ErrorHandling} from '../../../Classes/ErrorHandling';
import { ExternTransfersListService } from '../../../Services/extern-transfers-list.service';

@Component({
  selector: 'app-transfer-extern',
  templateUrl: './transfer-extern.component.html',
  styleUrls: ['./transfer-extern.component.css'],
  providers: [ExternTransfersListService]
})
export class TransferExternComponent implements OnInit {
  public loading = false;
  public displayedColumns = ['sender_code' , 'sender_bank', 'receiver_code' , 'receiver_bank', 'date', 'amount', 'status'];
  public dataSource: MatTableDataSource<Transfer>;
  public transfers: Transfer[] = [];
  public empty = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    public errorMsg: MatSnackBar,
    public transfersList: ExternTransfersListService
  ) { }

  public ngOnInit(): void{
    this.initListTransfer();
  }

  public initListTransfer(): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    
    this.transfersList.getTransfersList().subscribe(
      data => {
         this.transfers = data;
         if (this.transfers.length == 0) {
           this.empty = true;
         } else {
           this.empty = false;
         }
        // Assign the data to the data source for the table to render
        this.dataSource = new MatTableDataSource(this.transfers);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  public refresh(): void{
    this.initListTransfer();
  }
}
