import { RequestHandlerService } from './request-handler.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Manager } from '../Classes/Manager';

@Injectable()
export class ManagerProfileService {

  private tharwaURL = 'managers/'; /** DNS/manager/{id} to get the manager profile**/

  constructor(private http: RequestHandlerService) { }

  public getManagerProfile(id): Observable<Manager> {
    /** return the http get request response
     * the manager profile
     * or an error if it was occured
     * **/
    return this.http.httpGetRequest(this.tharwaURL + id);
  }

}
