import { Component, OnInit, Inject } from '@angular/core';
import { Manager } from '../../../Classes/Manager';
import { FormControl, Validators} from '@angular/forms';
import { InputValidation } from '../../../Classes/InputValidation';
import { ActivatedRoute } from '@angular/router';
import { ManagerProfileService } from '../../../Services/manager-profile.service';
import { MatDialog, MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ErrorHandling } from '../../../Classes/ErrorHandling';
import { EditProfileService } from '../../../Services/edit-profile.service';
import { ChangePasswordService } from '../../../Services/change-password.service';
import { RequestHandlerService } from '../../../Services/request-handler.service';


@Component({
  selector: 'app-manager-profile',
  templateUrl: './managerProfile.component.html',
  styleUrls: ['./managerProfile.component.css'],
  providers: [ManagerProfileService, EditProfileService, ChangePasswordService]
})
export class ManagerProfileComponent implements OnInit {
  public loading = false;
  public disable = true;
  public manager: Manager=new Manager(1,'','','','','','');
  public path = "";
  constructor(
    public errorMsg: MatSnackBar,
    private route: ActivatedRoute,
    private profile: ManagerProfileService,
    private pwdService: ChangePasswordService,
    public dialog: MatDialog,
    public editProfileService: EditProfileService,
    public api: RequestHandlerService
  ) { 
    this.path = this.api.tharwaURL + this.api.imgUrl;
  }

  public ngOnInit(): void{
    const id = +this.route.parent.snapshot.paramMap.get('id');
    this.initProfile(id);
  }

  public initProfile(id): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.profile.getManagerProfile(id).subscribe(
      data => {
        this.manager = data;
        this.manager.photo = this.api.tharwaURL + this.path + this.manager.photo;
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      }
    );
  }

  public editProfileDialog(): void{
    const dialogRef = this.dialog.open(EditManagerProfileComponent, {
      width: '50%',
      height: 'auto',
      disableClose: true,
      data : this.manager
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result!=-1) {
        this.editProfile(result)
      }
    });
  }

  public editProfile(manager): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.editProfileService.editManger(manager, this.manager.id).subscribe(
      data => {
        if (manager.photo) {
          this.editProfileService.postImage(manager.photo, this.manager.id).subscribe(
            response => {
              this.initProfile(this.manager.id);
              this.loading = false;
              msg.showErrorMsg('Photo has successfully updated', 200)
            },
            error => {
              msg.handleError(error);
              this.loading = false;
            }
          );
          this.loading = false;
          this.manager = manager;
          this.manager.photo = this.api.tharwaURL + this.path + this.manager.photo;
          msg.showErrorMsg('Profile has successfully updated', 200)
        }
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      })
  }

  public changePasswordDialog(): void{
    const dialogRef = this.dialog.open(ChangeManagerPasswordComponent, {
      width: '40rem',
      height: 'auto',
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result!=-1) {
          this.changePassword(result.oldPassword, result.newPassword);
        }
    });
  }

  public changePassword(oldPassword, newPassword): void{
    const msg = new ErrorHandling(this.errorMsg);
    this.loading = true;
    this.pwdService.changePassword(oldPassword, newPassword).subscribe(
      data => {
        msg.showErrorMsg('Password has successfully changed', 200)
        this.loading = false;
      },
      error => {
        msg.handleError(error);
        this.loading = false;
      });
  }
}

/** dynamic component : edit manager form dialog **/

@Component({
  selector: 'app-edit-manager-profile',
  templateUrl: 'editManagerProfile.html',
  styleUrls: ['editManagerProfile.css']
})


export class EditManagerProfileComponent implements  OnInit {
  public hide = true;
  imagefile:  File;
  filePreviewPath = '';
  nameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);
  nameMatcher = new InputValidation();
  firstnameControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[a-zA-Z][a-zA-Z ]+/)
  ]);
  firstnameMatcher = new InputValidation();

  emailControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/)
  ]);
  emailMatcher = new InputValidation();

  phoneControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/[0-9]*/),
    Validators.minLength(10),
    Validators.maxLength(10)
  ]);
  phoneMatcher = new InputValidation();

  addressControl = new FormControl('', [
    Validators.required
  ]);
  addressMatcher = new InputValidation();

  constructor(
    public errorMsg: MatSnackBar,
    public dialogRef: MatDialogRef<EditManagerProfileComponent>,
    @Inject(MAT_DIALOG_DATA) public manager: Manager
  ) {
    this.filePreviewPath = this.manager.photo;
  }

  onSubmit() {
    const manager = {
      id: this.manager.id,
      name : this.nameControl.value,
      email: this.emailControl.value,
      phone_number: this.phoneControl.value,
      address : this.addressControl.value,
      photo: this.imagefile
    };
    if (this.nameControl.pristine) {
      manager.name = this.manager.name;
    }
    if (this.emailControl.pristine) {
      manager.email = this.manager.email;
    }
    if (this.phoneControl.pristine) {
      manager.phone_number = this.manager.phone_number
      ;
    }
    if (this.addressControl.pristine) {
      manager.address = this.manager.address;
    }
    this.dialogRef.close(manager);
  }

  addImg(event: any) {
    this.imagefile = event.target.files.item(0);
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (event: any) => {
          this.filePreviewPath = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
  }
  }


  isImage(file: File): boolean {
    return /^image\//.test(file.type);
  }

  removeImage(event: any) {
    this.imagefile = null;
  }

  formatSize(bytes) {
    if (bytes === 0) {
      return '0 B';
    }
    const k = 1000,
      dm = 3,
      sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }

  ngOnInit() {}
}


/** dynamic component : change password dialog **/

@Component({
  selector: 'app-change-password',
  templateUrl: 'passwordManager.html',
  styleUrls: ['passwordManager.css']
})


export class ChangeManagerPasswordComponent {
  public hideold = true;
  public hidenew = true;
  public hideconf = true;

  oldPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherOldPassword = new InputValidation();

  confPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherConfPassword = new InputValidation();

  newPasswordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(8),
    Validators.pattern(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?!.*\s).{8,20}$/)
  ]);
  matcherNewPassword = new InputValidation();
  constructor(
    public dialogRef: MatDialogRef<ChangeManagerPasswordComponent>,
    public errorMsg: MatSnackBar,
  ){}

  public showNewPassword(): void {
    this.hidenew = !this.hidenew;
  }

  public showOldPassword(): void {
    this.hideold = !this.hideold;
  }
  
  public showConfPassword(): void {
    this.hideconf = !this.hideconf;
  }

  public onClose(): void{
    this.dialogRef.close(-1);
  }

  public validate(): void{
    if(this.newPasswordFormControl.value == this.confPasswordFormControl.value){
      const response = {
        oldPassword: this.oldPasswordFormControl.value,
        newPassword: this.newPasswordFormControl.value
      }
      this.dialogRef.close(response);
    } else {
      const msg = new ErrorHandling(this.errorMsg);
      msg.showErrorMsg('Passwords dont match', '=/=');
    }
  }
}