import { Routes } from '@angular/router';
// import all the sections needed.
import { BankersListComponent } from './bankers-list/bankers-list.component';
import { BanksComponent } from './banks/banks.component';
import { CommissionsComponent } from './commissions/commissions.component';
import { ManagerProfileComponent } from './managerProfile/managerProfile.component';
import { ManagerHomeComponent } from './manager-home/manager-home.component';


export const sectionRoutesManager: Routes = [
  {path: 'dashboard', component: ManagerHomeComponent},
  {path: 'bankers', component: BankersListComponent},
  {path: 'banks', component: BanksComponent},
  {path: 'commissions', component: CommissionsComponent},
  {path: 'profile', component: ManagerProfileComponent},
];


