import { ActivatedRouteSnapshot } from '@angular/router';
import { AuthorisationService } from './authorisation.service';
import { CanActivate } from '@angular/router';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { RouterStateSnapshot } from '@angular/router';

@Injectable()
export class AuthGuardService implements CanActivate {

  constructor(private authService: AuthorisationService, private router: Router) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    /** if the user is authenticated activate the route**/
    if (this.authService.access_token) { return true; }
    /** else redirect the user to login page **/
    this.router.navigate(['/home']);
    return false;
  }
}

