import { HttpErrorResponse } from '@angular/common/http';
import { MatSnackBar } from '@angular/material';

export class ErrorHandling {
    constructor(
        public errorMsg: MatSnackBar) {
        }
    public handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            this.showErrorMsg('Not known error', '###');
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            if ( error.status === 0) {
                this.showErrorMsg('network problem', '0');
            } else if ( error.status === 400) {
                this.showErrorMsg('invalid data', '400');
            } else if ( error.status === 401) {
                this.showErrorMsg('authentification failed', '401');
            } else if ( error.status === 403) {
                this.showErrorMsg('authorization failed', '403');
            } else if ( error.status === 404) {
                this.showErrorMsg('Not Found', '404');
            } else if ( error.status === 406) {
                this.showErrorMsg('Partial Content', '206');
            } else {
              this.showErrorMsg('unknown error', error.status);
            }
        }
    }

    showErrorMsg(message: string, status) {
        this.errorMsg.open(message, status, {
            duration: 5000,
        });
    }
}
